.banner {
    position: relative;
}
.banner__img {
    width: 100%;
}
.banner__text {

    position: absolute;
    bottom: 100px;
    left: 200px;
    display: flex;
    flex-direction: column;

    h2 {
        font-size: px-to-rem(80px);
        line-height: px-to-rem(120px);
        font-weight: 700;
    }
    p {
        font-size: px-to-rem(18px);
        line-height: px-to-rem(27px);
        font-weight: 500;
    }
}

.btn {
    @include button();
    width: 215px;
}

@media (max-width:1200px) {
    .banner__text {
        bottom: 50px;
        left: 50px;

        h2 {
            font-size: px-to-rem(60px);
            line-height: px-to-rem(90px);
            font-weight: 700;
        }
        p {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(23px);
            font-weight: 500;
        }
    }

    .btn {
        font-size: px-to-rem(16px);
        width: 170px;
    }

}

@media (max-width:640px) {

    .banner__wrap {
        margin-bottom: $height-mobile;
    }

    .banner__text {
        position: relative;
        bottom: 0;
        left: 0;
        flex-direction: column;
        text-align: center;
        margin: 30px 0 0 0;

        h2 {
            font-size: px-to-rem(40px);
            line-height: px-to-rem(60px);
            font-weight: 700;
        }
        
        p {
            font-size: px-to-rem(14px);
            line-height: px-to-rem(18px);
            font-weight: 500;
        }
    }

}