.offer {
    position: relative;
}
.offer__img {
    width: 100%;
}
.offer__text {
    position: absolute;
    bottom: 100px;
    right: 70px;
    display: flex;
    flex-direction: column;

    h1 {
        font-size: px-to-rem(80px);
        line-height: px-to-rem(120px);
        font-weight: 700;
    }
    p {
        font-size: px-to-rem(18px);
        line-height: px-to-rem(27px);
        font-weight: 500;
    }
}
.offer__buttons {
    margin-top: 27px;
    display: flex;
    flex-wrap: wrap;
    gap: 35px;
}

.btn {
    @include button();
}


@media (max-width:1200px) {
    .offer__text {
        bottom: 50px;
        right: 50px;

        h1 {
            font-size: px-to-rem(60px);
            line-height: px-to-rem(90px);
            font-weight: 700;
        }
        p {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(23px);
            font-weight: 500;
        }
    }

    .offer__buttons {
        margin-top: 20px;
        gap: 25px;
    }

    .btn {
        font-size: px-to-rem(16px);
        width: 130px;
    }

}

@media (max-width:900px) {



    .offer__text {
        flex-direction: row;
        align-items: center;
        column-gap: 30px;
    }

    .offer__buttons {
        flex-direction: column;
        gap: 20px;

        .btn {
            width: 130px;
        }
    }
}

@media (max-width:640px) {

    .offer__text {
        position: relative;
        bottom: 0;
        right: 0;
        flex-direction: column;
        text-align: center;
        margin: 30px 0 0 0;

        h1 {
            font-size: px-to-rem(40px);
            line-height: px-to-rem(60px);
            font-weight: 700;
        }
        p {
            font-size: px-to-rem(14px);
            line-height: px-to-rem(18px);
            font-weight: 500;
        }
    }

    .offer__buttons {
        flex-direction: row;
        gap: 20px;
    }
}
