.women__wrap , .men__wrap {
    margin-bottom: $height;
}

.section__title {

    text-align: center;

    h2 {
        font-size: px-to-rem(20px);
        font-weight: 700;
        line-height: px-to-rem(30px);
        margin-bottom: 15px;
    }

    p {
        margin-bottom: 72px;
    }
}

.fashion__block {
    display: flex;
    justify-content: space-between;
    column-gap: 20px;
}

.fashion__item {

    img {
        width: 100%;
    }

    .fashion__text {
        padding-top: 40px;
        padding-bottom: 20px;
        
        p {
            font-weight: 300;
        }

        h3 {
            font-weight: 600;
            font-size: px-to-rem(15px);
            margin: 3px 0;
        }
    }

    &:hover {
        box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
    }

    a:hover {
        text-decoration: none;
    }
}

.prices {
    display: flex;
    gap: 30px;
}
.old-price {
    text-decoration:  line-through;
}
.current-price {
    color: $red;
}

@media (max-width:800px) { 

    .fashion__block {
        flex-wrap: wrap;
        justify-content: center;
        row-gap: 30px;
    }
}

@media (max-width:640px) {

    .women__wrap , .men__wrap {
        margin-bottom: $height-mobile;
    }

    .section__title {
    
        p {
            margin-bottom: $height-mobile;
        }
    }
    
}