.contact__wrap {
    background-color: $blue;
    padding: $height $height-mobile;
}
.contact {
    display: flex;
    justify-content: space-between;
}

.contact__left {

    .contact__title {
        max-width: 400px;
    }

    .contact__text {
        margin: 18px 0 40px 0;
    }

}

.contact__email {
    margin: 16px 0 30px 0;
    width: max-content;
    position: relative;
    &::after {
        position: absolute;
        content: "";
        bottom: -5px;
        left: 0;
        height: 1px;
        width: 100%;
        border-bottom: 1px solid $black;
    }
}
.footer__social {
    display: flex;
    gap: 15px;
}

.contact__right {
}
.contact__form {
    margin: 24px 0 70px 0;
    max-width: 500px;
    display: flex;
    input {
        border-radius: 5px 0 0 5px;
        border: none;
        padding: 11px 25px;
        width: 100%;
    }
    button {
        border-radius: 0 5px 5px 0;
        border: none;
        background-color: $white;
        padding-right: 25px;
        font-weight: 600;
        cursor: pointer;
    }
}

.contact__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 80px;

    .contact__nav-title {
        font-weight: 600;
        font-size: px-to-rem(20px);
        line-height: px-to-rem(30px);
        display: grid;
        row-gap: 20px;
        cursor: pointer;
    }

    li {
        list-style: none;
        font-weight: 400;
        font-size: px-to-rem(15px);
        line-height: px-to-rem(22px);
    }
}

.copy__wrap {
    background-color: $blue2;
    padding: 35px 0;
    text-align: center;
}

.copy {
    display: grid;
    gap: 10px;
}

@media (max-width:1050px) {

    .contact {
        align-items: center;
        flex-direction: column;
    }

    .contact__left {
        max-width: 100%;
        display: flex;
        gap: 50px;
    }
    
}

@media (max-width:640px) {

    .contact__wrap {
        padding: $height-mobile 0;
    }

    .contact__left {
        display: block;
        text-align: center;
        margin-bottom: 30px;

        .contact__email {
            margin-left: auto;
            margin-right: auto;
        }

        .footer__social {
            justify-content: center;
        }
    }

    .contact__right {
        text-align: center;

    }

    .contact__nav {
        text-align: center;
        row-gap: 30px;
    }

    
}