.styles__wrap {
    margin-bottom: $height;
}

.styles__block {
    display: grid;
    position: relative;
    grid-template-columns: auto auto 300px auto;
    grid-template-areas: 
        'img-1 img-1 img-2 img-2'
        'img-1 img-1 img-4 img-3'
        ;
    justify-content: center;
    gap: 20px;
    img {
        width: 100%;
    }
}
.styles__item:nth-of-type(1) {
    grid-area: img-1;
    margin-top: 90px;
}
.styles__item:nth-of-type(2) {
    grid-area: img-2;
    padding-right: 20px;

}
.styles__item:nth-of-type(3) {
    position: absolute;
    bottom: 40px;
    right: -20px;
    grid-area: img-3;
    background-color: $white;
    padding: 20px 20px 0 20px;
}
.styles__item:nth-of-type(4) {
    grid-area: img-4;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;

    a {
        position: relative;

        &::after {
            position: absolute;
            content:"";
            left: 0;
            top: 25px;
            width: 100%;
            height: 1px;
            border-top: 1px solid $black;
        }

        &:hover {
            text-decoration: none;
        }

    }

}

@media (max-width:1000px) {
    
    .styles__block {
        grid-template-columns: auto;
        grid-template-areas: 
            'img-2 img-2 img-1 img-1'
            'img-3 img-4 img-1 img-1'
            ;
    }

    .styles__item:nth-of-type(2) {
        padding-right: 0;
        padding-left: 20px;
    }

    .styles__item:nth-of-type(3) {
        bottom: 0;
        right: 0;
        left: -20px;
        padding: 20px 20px 20px 20px;
    }

    .styles__item:nth-of-type(4) {
        justify-content: start;
    }

}

@media (max-width:585px) {

    .styles__block {
        grid-template-columns: auto;
        grid-template-areas: 
            'img-2 img-2'
            'img-3 img-4'
            'img-1 img-1'
            ;
    }

    .styles__item:nth-of-type(1) {
        margin-top: 0;
    }

    .styles__item:nth-of-type(2) {
        padding-left: 0;
    }

    .styles__item:nth-of-type(3) {
        position: relative;
        bottom: 0;
        right: 0;
        left: 0;
        padding: 0;
    }

    .styles__item:nth-of-type(4) {
        justify-content: center;
        align-items: center;
    }

    .styles__wrap {
        margin-bottom: 70px;
    }

}
