.catalog__wrap {
    margin-top: $height;
    margin-bottom: $height;
}

.catalog {
    display: grid;
    grid-template-areas: 
        'img-1 img-1 img-2 img-2'
        'img-3 img-4 img-2 img-2'
        ;
    justify-content: center;
    gap: 20px;

    img {
        width: 100%;
    }

}

.catalog__item {
    &:hover {
        box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
    }
}

.catalog__item:nth-of-type(1) {
    grid-area: img-1;
}
.catalog__item:nth-of-type(2) {
    grid-area: img-2;
    position: relative;

    .btn {
        position: absolute;
        bottom: 30px;
        left: 30px;
        @include button($white,$black,false);
        width: 200px;
        
    }

}

.catalog__item:nth-of-type(3) {
    grid-area: img-3;
}

.catalog__item:nth-of-type(4) {
    grid-area: img-4;
}

.catalog__item:nth-of-type(2)::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #00000052;
    position: absolute;
    left: 0;
    top: 0;
}

@media (max-width:800px) {

    .catalog {

        grid-template-areas: 
            'img-2 img-2'
            'img-3 img-4'
            'img-1 img-1'
            ;    
    }    
}

@media (max-width:640px) {

    .catalog__wrap {
        margin-top: $height-mobile;
        margin-bottom: $height-mobile;
    }
    
}

@media (max-width:510px) {

    .catalog__item:nth-of-type(2) {
    
        .btn {
            bottom: 20px;
            left: 20px;
            @include button($white,$black,false);
            width: 150px;
            font-size: px-to-rem(14px);
        }
    
    }
    
}