.header__wrap {
    background-color: $gray;
}
.header {
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;
}

.header__logo {
    font-size: px-to-rem(28px);
    font-weight: 700;
}

.header__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.header__buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

@media (max-width:800px) {

    .header {
        flex-direction: column;
        row-gap: 20px;
    }
    
}
