@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 15px;
  line-height: 1.375rem;
  color: #000;
}

body {
  font-family: "Poppins", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 400;
  color: #22262A;
}

a {
  text-decoration: none;
  color: #22262A;
}
a:hover {
  text-decoration: underline;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1170px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header__wrap {
  background-color: #ededed;
}

.header {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  font-size: 1.75rem;
  font-weight: 700;
}

.header__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.header__buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

@media (max-width: 800px) {
  .header {
    flex-direction: column;
    row-gap: 20px;
  }
}
.offer {
  position: relative;
}

.offer__img {
  width: 100%;
}

.offer__text {
  position: absolute;
  bottom: 100px;
  right: 70px;
  display: flex;
  flex-direction: column;
}
.offer__text h1 {
  font-size: 5rem;
  line-height: 7.5rem;
  font-weight: 700;
}
.offer__text p {
  font-size: 1.125rem;
  line-height: 1.6875rem;
  font-weight: 500;
}

.offer__buttons {
  margin-top: 27px;
  display: flex;
  flex-wrap: wrap;
  gap: 35px;
}

.btn {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  font-weight: 500;
  padding: 8px 0;
  width: 147px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #22262A;
}
.btn:hover {
  background-color: black;
  text-decoration: none;
}

@media (max-width: 1200px) {
  .offer__text {
    bottom: 50px;
    right: 50px;
  }
  .offer__text h1 {
    font-size: 3.75rem;
    line-height: 5.625rem;
    font-weight: 700;
  }
  .offer__text p {
    font-size: 1rem;
    line-height: 1.4375rem;
    font-weight: 500;
  }

  .offer__buttons {
    margin-top: 20px;
    gap: 25px;
  }

  .btn {
    font-size: 1rem;
    width: 130px;
  }
}
@media (max-width: 900px) {
  .offer__text {
    flex-direction: row;
    align-items: center;
    column-gap: 30px;
  }

  .offer__buttons {
    flex-direction: column;
    gap: 20px;
  }
  .offer__buttons .btn {
    width: 130px;
  }
}
@media (max-width: 640px) {
  .offer__text {
    position: relative;
    bottom: 0;
    right: 0;
    flex-direction: column;
    text-align: center;
    margin: 30px 0 0 0;
  }
  .offer__text h1 {
    font-size: 2.5rem;
    line-height: 3.75rem;
    font-weight: 700;
  }
  .offer__text p {
    font-size: 0.875rem;
    line-height: 1.125rem;
    font-weight: 500;
  }

  .offer__buttons {
    flex-direction: row;
    gap: 20px;
  }
}
.catalog__wrap {
  margin-top: 110px;
  margin-bottom: 110px;
}

.catalog {
  display: grid;
  grid-template-areas: "img-1 img-1 img-2 img-2" "img-3 img-4 img-2 img-2";
  justify-content: center;
  gap: 20px;
}
.catalog img {
  width: 100%;
}

.catalog__item:hover {
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}

.catalog__item:nth-of-type(1) {
  grid-area: img-1;
}

.catalog__item:nth-of-type(2) {
  grid-area: img-2;
  position: relative;
}
.catalog__item:nth-of-type(2) .btn {
  position: absolute;
  bottom: 30px;
  left: 30px;
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #000;
  font-weight: 500;
  padding: 8px 0;
  width: 147px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #fff;
  width: 200px;
}
.catalog__item:nth-of-type(2) .btn:hover {
  background-color: #cccccc;
  text-decoration: none;
}

.catalog__item:nth-of-type(3) {
  grid-area: img-3;
}

.catalog__item:nth-of-type(4) {
  grid-area: img-4;
}

.catalog__item:nth-of-type(2)::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #00000052;
  position: absolute;
  left: 0;
  top: 0;
}

@media (max-width: 800px) {
  .catalog {
    grid-template-areas: "img-2 img-2" "img-3 img-4" "img-1 img-1";
  }
}
@media (max-width: 640px) {
  .catalog__wrap {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
@media (max-width: 510px) {
  .catalog__item:nth-of-type(2) .btn {
    bottom: 20px;
    left: 20px;
    text-decoration: none;
    box-sizing: border-box;
    text-transform: uppercase;
    text-align: center;
    color: #000;
    font-weight: 500;
    padding: 8px 0;
    width: 147px;
    font-size: 1.125rem;
    display: inline-block;
    background-color: #fff;
    width: 150px;
    font-size: 0.875rem;
  }
  .catalog__item:nth-of-type(2) .btn:hover {
    background-color: #cccccc;
    text-decoration: none;
  }
}
.women__wrap, .men__wrap {
  margin-bottom: 110px;
}

.section__title {
  text-align: center;
}
.section__title h2 {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.875rem;
  margin-bottom: 15px;
}
.section__title p {
  margin-bottom: 72px;
}

.fashion__block {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.fashion__item img {
  width: 100%;
}
.fashion__item .fashion__text {
  padding-top: 40px;
  padding-bottom: 20px;
}
.fashion__item .fashion__text p {
  font-weight: 300;
}
.fashion__item .fashion__text h3 {
  font-weight: 600;
  font-size: 0.9375rem;
  margin: 3px 0;
}
.fashion__item:hover {
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}
.fashion__item a:hover {
  text-decoration: none;
}

.prices {
  display: flex;
  gap: 30px;
}

.old-price {
  text-decoration: line-through;
}

.current-price {
  color: #CF2929;
}

@media (max-width: 800px) {
  .fashion__block {
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 30px;
  }
}
@media (max-width: 640px) {
  .women__wrap, .men__wrap {
    margin-bottom: 50px;
  }

  .section__title p {
    margin-bottom: 50px;
  }
}
.styles__wrap {
  margin-bottom: 110px;
}

.styles__block {
  display: grid;
  position: relative;
  grid-template-columns: auto auto 300px auto;
  grid-template-areas: "img-1 img-1 img-2 img-2" "img-1 img-1 img-4 img-3";
  justify-content: center;
  gap: 20px;
}
.styles__block img {
  width: 100%;
}

.styles__item:nth-of-type(1) {
  grid-area: img-1;
  margin-top: 90px;
}

.styles__item:nth-of-type(2) {
  grid-area: img-2;
  padding-right: 20px;
}

.styles__item:nth-of-type(3) {
  position: absolute;
  bottom: 40px;
  right: -20px;
  grid-area: img-3;
  background-color: #fff;
  padding: 20px 20px 0 20px;
}

.styles__item:nth-of-type(4) {
  grid-area: img-4;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
}
.styles__item:nth-of-type(4) a {
  position: relative;
}
.styles__item:nth-of-type(4) a::after {
  position: absolute;
  content: "";
  left: 0;
  top: 25px;
  width: 100%;
  height: 1px;
  border-top: 1px solid #000;
}
.styles__item:nth-of-type(4) a:hover {
  text-decoration: none;
}

@media (max-width: 1000px) {
  .styles__block {
    grid-template-columns: auto;
    grid-template-areas: "img-2 img-2 img-1 img-1" "img-3 img-4 img-1 img-1";
  }

  .styles__item:nth-of-type(2) {
    padding-right: 0;
    padding-left: 20px;
  }

  .styles__item:nth-of-type(3) {
    bottom: 0;
    right: 0;
    left: -20px;
    padding: 20px 20px 20px 20px;
  }

  .styles__item:nth-of-type(4) {
    justify-content: start;
  }
}
@media (max-width: 585px) {
  .styles__block {
    grid-template-columns: auto;
    grid-template-areas: "img-2 img-2" "img-3 img-4" "img-1 img-1";
  }

  .styles__item:nth-of-type(1) {
    margin-top: 0;
  }

  .styles__item:nth-of-type(2) {
    padding-left: 0;
  }

  .styles__item:nth-of-type(3) {
    position: relative;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 0;
  }

  .styles__item:nth-of-type(4) {
    justify-content: center;
    align-items: center;
  }

  .styles__wrap {
    margin-bottom: 70px;
  }
}
