.contact__wrap {
  background-color: #F2F8FC;
  padding: 110px 50px;
}

.contact {
  display: flex;
  justify-content: space-between;
}

.contact__left .contact__title {
  max-width: 400px;
}
.contact__left .contact__text {
  margin: 18px 0 40px 0;
}

.contact__email {
  margin: 16px 0 30px 0;
  width: max-content;
  position: relative;
}
.contact__email::after {
  position: absolute;
  content: "";
  bottom: -5px;
  left: 0;
  height: 1px;
  width: 100%;
  border-bottom: 1px solid #000;
}

.footer__social {
  display: flex;
  gap: 15px;
}

.contact__form {
  margin: 24px 0 70px 0;
  max-width: 500px;
  display: flex;
}
.contact__form input {
  border-radius: 5px 0 0 5px;
  border: none;
  padding: 11px 25px;
  width: 100%;
}
.contact__form button {
  border-radius: 0 5px 5px 0;
  border: none;
  background-color: #fff;
  padding-right: 25px;
  font-weight: 600;
  cursor: pointer;
}

.contact__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 80px;
}
.contact__nav .contact__nav-title {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.875rem;
  display: grid;
  row-gap: 20px;
  cursor: pointer;
}
.contact__nav li {
  list-style: none;
  font-weight: 400;
  font-size: 0.9375rem;
  line-height: 1.375rem;
}

.copy__wrap {
  background-color: #E7F5F8;
  padding: 35px 0;
  text-align: center;
}

.copy {
  display: grid;
  gap: 10px;
}

@media (max-width: 1050px) {
  .contact {
    align-items: center;
    flex-direction: column;
  }

  .contact__left {
    max-width: 100%;
    display: flex;
    gap: 50px;
  }
}
@media (max-width: 640px) {
  .contact__wrap {
    padding: 50px 0;
  }

  .contact__left {
    display: block;
    text-align: center;
    margin-bottom: 30px;
  }
  .contact__left .contact__email {
    margin-left: auto;
    margin-right: auto;
  }
  .contact__left .footer__social {
    justify-content: center;
  }

  .contact__right {
    text-align: center;
  }

  .contact__nav {
    text-align: center;
    row-gap: 30px;
  }
}